import {useEffect, useState} from "react";
import BasicTable from "./BasicTable";
import Expenses from "../services/Expenses";
import AddExpense from "./AddExpense";


const Expense = (props) => {
    const [expenses, setExpenses] = useState([])

    useEffect(() => {
        const fetchData = async () => {
            const expenses = await Expenses.getAll()
            setExpenses(expenses)
            // const cakes = await Cakes.getAll()
            // setCakes(cakes)
        }
        fetchData()
    }, [])



    const columns = [
        {label: "Date", accessor: "date"},
        {label: "Item", accessor: "item"},
        {label: "Cost", accessor: "cost"},
        {label: "Category", accessor: "category"}
    ];

    return (
        <div>
            <BasicTable columns={columns} data={expenses} setData={setExpenses}></BasicTable>
            <AddExpense setMessage={props.setMessage} setExpenses={setExpenses} expenses={expenses}/>
        </div>
    )

}
export default Expense
