import {useDate, useDropdown, useField} from "../hooks";
import 'react-datepicker/dist/react-datepicker.css'

import {useEffect, useState} from "react";
import BasicTable from "./BasicTable";
import Incomes from "../services/Incomes";
import {Box, Button, InputLabel, TextField} from "@mui/material";
import Cakes from "../services/Cakes";


const Income = (props) => {
    const [incomes, setIncomes] = useState([])
    const [cakes, setCakes] = useState([])

    useEffect(() => {
        const fetchData = async () => {
            const incomes = await Incomes.getAll();
            setIncomes(incomes)
            const cakes = await Cakes.getAll()
            setCakes(cakes)
        }
        fetchData()
    }, [])

    const categoryOptions = ['Cake Sales','Custom Cake Order','Supply Sales', 'Teaching', 'Other' ]
    const cakeList = cakes?.map(c=>c.name)

    const {reset: resetDate, DateInput:SelectDate,value:date} = useDate()
    const {reset: resetAmount, ...amount} = useField('number', '')
    const {
        reset: resetSource,
        selectedValue: source,
        Dropdown: CategoryDropdown,
    } = useDropdown( categoryOptions);

    const {
        reset: resetCakes,
        selectedValue: selectedCake,
        Dropdown: CakesDropdown
    } = useDropdown(cakeList);
    const addNew = (income) => {
        Incomes.create(income).then(()=>{
            setIncomes(incomes.concat(income))
        }).catch((error) => {
            props.setMessage(error.message)
        });
    }
    const handleSubmit = (e) => {
        e.preventDefault()
        let incomeObject = {
            date: date.toString(),
            amount: amount.value,
            source: source,
        }
        if(selectedCake!==null)
            cakes.find(cake=>cake.name===selectedCake)

        addNew(
            incomeObject
        )
        props.setMessage(`A new income of $${amount.value} is added!`)
        clearInputs(e)

    }
    const clearInputs = (e) => {
        e.preventDefault()
        resetDate()
        resetAmount()
        resetSource()
        resetCakes()
    }
    const columns = [
        {label: "Date", accessor: "date"},
        {label: "Source", accessor: "source"},
        {label: "Amount", accessor: "amount"}
    ];
    const getLinkedCake=()=>{
        if(source === "Cake Sales" && cakeList.length !== 0)
        return(<><InputLabel>Cake:</InputLabel>
        <CakesDropdown required/></>)
    }

    return (
        <div>
            <BasicTable columns={columns} data={incomes} setData={setIncomes}></BasicTable>
            <h2>Add a new income</h2>
                    <Box component="form">
                        <InputLabel>Date:</InputLabel><SelectDate required/>
                        <InputLabel>Source:</InputLabel>
                        <CategoryDropdown required/>
                        {getLinkedCake()}
                        <InputLabel>Amount:</InputLabel>
                        <TextField variant="outlined"
                                   label="Accepts only numbers"
                                   placeholder="Enter amount here"
                                   type="number"
                                   inputProps={{ maxLength: 5 }}
                                   fullWidth
                            required {...amount}/>

                    <Button onClick={handleSubmit} variant="contained" color="primary" >Create</Button>
                    <Button onClick={clearInputs} variant="outlined" color="primary" >Reset</Button>
        </Box>
        </div>
    )

}
export default Income
