import {useEffect, useState} from 'react'
import DatePicker from "react-datepicker";
import {FormControl, MenuItem} from "@mui/material";
import { Select } from '@mui/material';

export const useField = (type,defaultValue) => {
    const [value, setValue] = useState(defaultValue)

    const onChange = (event) => {
        const input=event.target.value
        // if (type === "number" && isNaN(Number(input))) {
        //     return(<Notification notification="Provide a valid number"/> )
        // }
        setValue(input)
    }
    const reset = () => {
        setValue(defaultValue)
    }

    return {
        type,
        value,
        onChange,
        reset
    }
}

export const useDropdown = ( options) => {
    const [value, setValue] = useState(options.length>0 ?options[0]:"")
    // const [value, setValue] = useState(options[0])

    const reset = () => {
        setValue(options[0])
    }

    const Dropdown = () => (
        <Select
            defaultValue={value}
            onChange={(event) => setValue(event.target.value)}
        >
            {options.map((option) => (
                <MenuItem key={option} value={option}>
                    {option}
                </MenuItem>
            ))}
        </Select>
    )
    return {
        selectedValue: value,
        Dropdown,
        reset,
    }
}

export const useDropdownWithSort = ( options,data,setData) => {
    const [value, setValue] = useState(options[0].accessor)
    const orderByOptions = [
        {value: 'asc', label: 'A-Z'},
        {value: 'desc', label: 'Z-A'}]
    const [sortOrder, setSortOrder] = useState(orderByOptions[0].value);
    const reset = () => {
        setValue(options[0].accessor)
    }

    useEffect(()=>{
    arrangeData()
    },[value,sortOrder,setData])

    const Dropdown = () => (<div>
        <FormControl sx={{ m: 1, minWidth: 120 }}>
            Sort By: <Select   labelId="demo-simple-select-label"
                               id="demo-simple-select"
            defaultValue={value}
            onChange={(event) => setValue(event.target.value)}
        >
            {options.map((option) => (<MenuItem key={option.accessor} value={option.accessor}>
                {option.label}
            </MenuItem>))}
        </Select></FormControl>
            <FormControl sx={{ m: 1, minWidth: 120 }}>
            Order By: <Select
            defaultValue={sortOrder}
            onChange={(event) => setSortOrder(event.target.value)}
        >
            {orderByOptions.map((option) => (<MenuItem key={option.value} value={option.value}>
                {option.label}
            </MenuItem>))}
        </Select>
        </FormControl></div>

    )

    const arrangeData=()=>{
        let sortedData = [...data];

        if (sortOrder === "asc") {
            sortedData.sort((a, b) => (a[value] > b[value] ? 1 : -1));
            setSortOrder("desc");
        } else {
            sortedData.sort((a, b) => (a[value] < b[value] ? 1 : -1));
            setSortOrder("asc");
        }
        setSortOrder(sortOrder)
        setData(sortedData);
    }

    return {
        selectedValue: value,
        Dropdown,
        reset,
        arrangeData
    }
}

export const useDate = () => {
    const [value, setValue] = useState(new Date());

    const reset = () => {
        setValue(new Date())
    }

    const DateInput = () => (
        <DatePicker
            autoOk
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    label="Select a date"
                    disabled={false}
                    fullWidth
            selected={value}
            onChange={date => setValue(date)}
        />
    )
    const formatDate = (date) => {
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        return `${year}-${month.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')}`;
    };
    return {
        value:formatDate(value),
        DateInput,
        reset,
    }
}

