import {createTheme} from '@material-ui/core/styles';
import {green, purple} from '@mui/material/colors';

const theme = createTheme({
        typography: {
            fontFamily: [
                'Roboto',
                '-apple-system',
                'BlinkMacSystemFont',
                '"Segoe UI"',
                'Arial',
                'sans-serif',
                '"Apple Color Emoji"',
                '"Segoe UI Emoji"',
                '"Segoe UI Symbol"',
            ].join(','),
        }, palette: {
            primary: {
                main: green[300]
            },
            secondary: {
                main: purple[600]
            },
            primaryLight: {
                main: green[50]
            }, secondaryLight: {
                main: purple[300]
            },
        }, h1: {
            fontSize: '2rem',
            fontWeight: 'bold',
            marginBottom: '1rem',
        },
        body1: {
            fontSize: '1rem',
            lineHeight: '1.5',
            marginBottom: '1rem',
        },
        button: {
            textTransform: 'none',
            fontSize: '1rem',
            padding: '0.5rem 1rem',
        },
    spacing: 8
    });

export default theme