import { useField} from "../hooks";
import 'react-datepicker/dist/react-datepicker.css'

import {useEffect, useState} from "react";
import BasicTable from "./BasicTable";
import {Box, Button, InputLabel, TextField} from "@mui/material";
import Cakes from "../services/Cakes";


const Cake = (props) => {
    const [cakes, setCakes] = useState([])
    useEffect(() => {
        Cakes.getAll().then(res => setCakes(res))
    }, [])
    const {reset: resetName, ...name} = useField('text', '')
    const {reset: resetPrice, ...price} = useField('number', '')

    const addNew = (cake) => {
        Cakes.create(cake).then(()=>{
            setCakes(cakes.concat(cake))
        }).catch((error) => {
            props.setMessage(error.message);
        });
    }
    const handleSubmit = (e) => {
        e.preventDefault()
        let cakeObject = {
            name:name.value,
            price: price.value,
        }
        addNew(
            cakeObject
        )
        props.setMessage(`A new cake ${name.value} of price $${price.value} is added!`)
        clearInputs(e)

    }
    const clearInputs = (e) => {
        e.preventDefault()
        resetPrice()
        resetName()
    }
    const columns = [
        {label: "Name", accessor: "name"},
        {label: "Price", accessor: "price"}
    ];

    return (
        <div>
            <BasicTable columns={columns} data={cakes} setData={setCakes}></BasicTable>
            <h2>Add a new Cake</h2>
                    <Box component="form">
                        <InputLabel>Name:</InputLabel>
                        <TextField variant="outlined"
                                   label="Accepts only text"
                                   placeholder="Enter cake name here"
                                   type="text"
                                   inputProps={{ maxLength: 15 }}
                                   fullWidth
                                   required {...name}/>
                        <InputLabel>Price:</InputLabel>
                        <TextField variant="outlined"
                                   label="Accepts only numbers"
                                   placeholder="Enter amount here"
                                   type="number"
                                   inputProps={{ maxLength: 5 }}
                                   fullWidth
                            required {...price}/>

                    <Button onClick={handleSubmit} variant="contained" color="primary" >Create</Button>
                    <Button onClick={clearInputs} variant="outlined" color="primary" >Reset</Button>
        </Box>
        </div>
    )

}
export default Cake
