import {useEffect, useState} from 'react'
import {Link, Route, Routes} from "react-router-dom";
import Profits from "./components/Profits";
import Expense from "./components/Expense";
import Income from "./components/Income";
import {AppBar, Button, IconButton, Paper, Toolbar, Typography} from "@mui/material";
import theme from "./theme";
import {Notification} from "./Notification";
import Cake from "./components/Cake";


const About = () =>( <Paper style={{ padding: 16 }}>
    <Typography variant="h4" gutterBottom>
        Get to Know Us
    </Typography>
        <Typography variant="body1" gutterBottom>
            Did you know that the ancient Egyptians were some of the earliest bakers? They even had professional bakers who were paid in bread and beer! We like to think that not much has changed since then - except maybe the addition of chocolate chips and rainbow sprinkles
        </Typography>
        <Typography variant="body1" paragraph>
            Are you tired of losing track of your baking expenses? Look no further! Our website offers a simple and easy way to track your expenses and keep your baking budget under control.
        </Typography>
        <Typography variant="body1" paragraph>
            Here at our home baking expense tracking website, we're all about helping you keep track of your baking expenses and ensuring that your passion for baking doesn't break the bank. Because let's face it, there's no such thing as too much butter or sugar, but there is such a thing as a budget.        </Typography>
        <Typography variant="body1" paragraph>
            So go ahead, whip up a batch of your favorite cookies and let us take care of the number crunching. After all, baking is all about having fun and indulging in the sweeter things in life. And if anyone tells you otherwise, just offer them a freshly baked cookie and watch as their frown turns upside down!"
        </Typography>
    </Paper>
)

const Footer = () => (
    <div>
        <hr/>
        Bake Track app for Home Bakers
    </div>
)


const App = () => {

    const [message, setMessage] = useState(null)
    const [type, setType] = useState(null)

    useEffect(() => {
        document.title = "Bake Track";
    })

    useEffect(() => {
    },[])
    useEffect(()=>{
        if(message!==null)
            setTimeout(() => setMessage(null), 5000)    },[message])
    const padding = {
        paddingRight: 5
    }


    return (

         <div style={{ fontFamily: theme.typography.fontFamily }}>
            <h1>The Baking Hub</h1>

             <Notification message={message}/>
             <AppBar position="static" color="primaryLight">
                <Toolbar>
                    <IconButton edge="start" color="secondary" aria-label="menu">
                    </IconButton>
                    <Button color="inherit" component={Link} to="/profits">
                        Profits </Button>
                    <Button color="inherit" component={Link} to="/expense">
                       Expense </Button>
                    <Button color="inherit" component={Link} to="/income">
                        Income</Button>
                    <Button color="inherit" component={Link} to="/cake">
                        Cake</Button>
                    <Button color="inherit" component={Link} to="/">About</Button>
                </Toolbar>
            </AppBar>
            <Notification notification={message} type={type}/>
            <Routes>
                <Route path="/profits" element={<Profits/>} />
                <Route path="/expense" element={<Expense setMessage={setMessage} setType={setType}/>} />
                <Route path="/income" element={<Income setMessage={setMessage}/>} />
                <Route path="/cake" element={<Cake setMessage={setMessage}/>} />

                <Route path="/" element={<About />} />
            </Routes>
            <Footer />
        </div>
    )
}

export default App
