import {Box, Button, InputLabel, TextField} from "@mui/material";
import {useDate, useDropdown, useField} from "../hooks";
import Expenses from "../services/Expenses";
import {useEffect, useState} from "react";
import Cakes from "../services/Cakes";

const AddExpense=({setMessage,setExpenses,expenses})=>{
    const [cakes, setCakes] = useState([])
    useEffect(() => {
        const fetchData = async () => {
            const cakes = await Cakes.getAll()
            setCakes(cakes)
        }
        fetchData()
    }, [])
    const categoryOptions = ['Ingredients', 'Equipment', 'Packaging', 'Other'
    ]


    const getCakeNames= () => {
        return cakes.map(c=>c.name)
    }

    const {reset: resetDate, DateInput: SelectDate, value: date} = useDate()

    const {reset: resetItem, ...item} = useField('text', '')
    const {reset: resetCost, ...cost} = useField('number', '')
    const {
        reset: resetCategory,
        selectedValue: category,
        Dropdown: CategoryDropdown
    } = useDropdown(categoryOptions);

    const {
        reset: resetCakes,
        selectedValue: selectedCake,
        Dropdown: CakesDropdown
    } = useDropdown(getCakeNames());

    const addNew = (expense) => {
        Expenses.create(expense).then(()=>{
            setExpenses(expenses.concat(expense))
        }).catch((error) => {
            setMessage(error.message);
        });


    }
    const handleSubmit = (e) => {
        e.preventDefault()

        let expenseObject = {
            date: date.toString(),
            item: item.value,
            cost: cost.value,
            category: category,
            linkedCake: cakes.find(cake=>cake.name===selectedCake)
        }
        console.log(selectedCake)
        addNew(
            expenseObject,date
        )
        setMessage(`A new item ${item.value} is created!`)
        clearInputs(e)

    }
    const clearInputs = (e) => {
        e.preventDefault()
        resetDate()
        resetItem()
        resetCost()
        resetCategory()
        resetCakes()
    }

    return(<><div><h2>Add a new expense</h2>
        <Box  component="form">
            <InputLabel>Date:</InputLabel>
            {/*<input {...date}/>*/}
            <SelectDate required/>
            <InputLabel>Item:</InputLabel>
            <TextField size="small"
                       variant="filled"
                       label="Accepts text"
                       placeholder="Enter item name here"
                       type="text"
                       inputProps={{ maxLength: 10 }}
                       fullWidth
                       required {...item}/>

            <InputLabel>Cost:</InputLabel>
            <TextField
                variant="outlined"
                label="Accepts only numbers"
                placeholder="Enter cost here"
                type="number"
                inputProps={{ maxLength: 5 }}
                fullWidth
                required {...cost}/>

            <InputLabel>Category:</InputLabel>
            <CategoryDropdown/>
            <InputLabel>Cake:</InputLabel>
            <CakesDropdown required/>
            <Button onClick={handleSubmit} variant="contained" color="primary" >Create</Button>
            <Button onClick={clearInputs} variant="outlined" color="primary" >Reset</Button>
        </Box>
    </div></>)
}
export default AddExpense